		"messages": [
			{
				"time": "fall 1903",
				"from": "France",
				"body": "Lähestyin teitä tällä kirjeellä kysyäksenne tahtoa yhteistoimintaan jatkossa Manner-Euroopan hallitsemiseksi. Joukkonne ovat hyvin etäällä, mutta jos kykenemme partisaanijoukoillamme hidastamaan Saksan ja Italian etenemistä, voimme tukea teitä maihinnousussanne.\n\n- Le Ombre",
				"id": "b17647d6a1ce5795267d29b3c6049d1852488e11"
			}
		]
