				"body": "Sain tiedon diplomaattisista kanavistani, että Saksa ja Venäjä ovat puhuneet epäpyhästä allianssista. Ensimmäisessä vaiheessa Venäjän pohjoinen armeijakunta tekee maihinnousun Ruotsiin ja Saksa vastaavasti valtaa Tanskan. Tämän jälkeen vuorossa olisi Norja ja Benelux-maat.",
